.section {
    font-family: "Cormorant Garamond", serif;
    display: flex;
    flex-direction: column;
    margin-bottom: 100px;
}
.container {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 70vh;
}
.textBox{
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: center;
    justify-items: center;
    align-items: center;
}
.imageContainer {
    width: 60%;
    height: 100%;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
}
.img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.h1 {
    font-size: 5vh;
}
.title {
    width: 80%;
}


.h2{
    font-size: 2vw;
    text-transform: uppercase;
}
.container2 {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 80vh;
}
.textBox2{
    display: flex;
    flex-direction: column;
    width: 70%;
    justify-items: center;
    align-items: center;
    margin-top: 40px;
}
.title2{
    width: 80%;
    border-top: 2px solid black;
}
.imageContainer2 {
    width: 30%;
    height: 100%;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
}

.text {
    font-size: 2vh;
    width: 60%;
    list-style-type: none;
    line-height: 2.5vh;
    text-align: center;

}
.li {
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.icon {
    width: 20px;
    height: 20px;
}
.p {
    width: 80%;
    font-size: 1.5vh;
}

.ul {
    font-size: 1.5vh;
    width: 80%;
    list-style-type: none;
    gap:20px;
    display: flex;
    flex-direction: column;
}
.liItem {

}

.imageContainer3 {
    width: 30%;
    height: 100%;
    margin-left: 5%;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
}
.imageContainer4 {
    width: 50%;
    height: 45%;
}
.textBox3 {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-items: center;
    align-items: center;
    margin-top: 40px;
}
.lineBox {
    display: flex;
    flex-direction: row;
    width: 80%;
    gap: 10px;
    height: 28%;
}

.container3 {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 45vh;
    align-items: center;
}
.imageContainer5 {
    width: 15%;
}
.container4 {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 70vh;
    align-items: center;
    justify-content: center;
    align-content: center;

}

.elemBox {
    display: flex;
    flex-direction: row;
    gap: 20px;
    width: 80%;

}
.text4 {
    font-size: 2vh;
    width: 60%;
    list-style-type: none;
    line-height: 2.5vh;
    text-align: center;
    align-self: center;

}

@media only screen and (max-width: 767px) {
    .h1 {
        font-size: 5vw;
    }
    .text {
        font-size: 3.5vw;
        width: 80%;
        line-height: 3.8vw;

    }
    .container {
        flex-direction: column;
        width: 100%;
        height: auto;
        align-items: center;
    }
    .imgMargin {
        z-index: -10;
        margin-top: -400px;
        opacity: 0.75;
    }
    .imageContainer {
        width: 100%;
    }
    .p {
        width: 100%;
        font-size: 3vw;
    }
    .h2{
        font-size: 4vw;
        text-transform: uppercase;
    }
    .title2{
        width: 100%;
        border-top: 2px solid black;
    }
    .ul {
        font-size: 3vw;
        width: 100%;
    }

    .textBox2{
        width: 80%;
        align-self: center;
    }
    .container2 {
        flex-direction: column;
        height: auto;
        width: 100%;
        justify-content: center;
    }
    .imgMargin2 {
        display: none;
    }
    .section {
        margin-bottom: 0px;
    }
    .imageContainer5 {
        width: 100px;
        height: 130px;
    }
    .text4 {
        font-size: 3vw;
        width: 70%;
        line-height: 3.8vw;

    }
    .container4 {;
        height: 45vh;

    }
}